.QuizCard {
  height: 100%;
  padding-top: 70px;

  @media screen and (max-width: 1025px) {
    padding-top: 60px;
  }

  .container {
    height: 100%;
  }

  &__wrapper {
    display: flex;
    justify-content: space-between;
    height: 100%;

    @media screen and (max-width: 1025px) {
      flex-direction: column;
      align-items: center;
    }
  }

  &__text {
    width: 100%;
    max-width: 450px;

    @media screen and (max-width: 1025px) {
      max-width: 100%;
      text-align: center;
    }

    @media screen and (max-width: 480px) {
      max-width: 320px;
      text-align: center;
    }
  }

  &__title {
    margin-bottom: 34px;
    font-family: "Grtsk Giga";
    font-size: 54px;
    font-weight: 700;
    text-transform: uppercase;
    letter-spacing: -0.03em;

    @media screen and (max-width: 1025px) {
      margin-bottom: 16px;
      font-size: 32px;
    }
  }

  &__tip {
    display: flex;
    align-items: center;
    gap: 10px;
    font-size: 16px;
    text-transform: uppercase;
    font-family: "Barlow";

    @media screen and (max-width: 1025px) {
      font-size: 12px;
      justify-content: center;
    }

    &:before {
      content: "";
      width: 12px;
      height: 12px;
      display: inline-block;
      vertical-align: middle;
      background: url("../../images/star2.svg");
    }
  }

  &__options {
    display: flex;
    width: 100%;
    max-width: 678px;
    max-height: 720px;
    margin-top: -125px;
    gap: 10px;
    position: relative;
    z-index: 1;

    @media screen and (max-width: 1025px) {
      flex-direction: column;
      max-width: 100%;
      margin-top: auto;
    }
  }

  &__option {
    flex: 1 0 calc(50% - 10px);
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0 40px;
    background: #fff;
    font-family: "Grtsk Zetta";
    font-size: 32px;
    font-weight: 700;
    text-align: center;
    letter-spacing: -0.03em;
    text-transform: uppercase;
    cursor: pointer;
    transition: background-color 0.5s ease;
    position: relative;
    border-radius: 30px;
    border: 1px solid #dddddd;

    @media screen and (max-width: 1025px) {
      height: 150px;
      flex-basis: auto;
      border-radius: 5px;
      font-size: 18px;
    }

    &::after {
      content: "";
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      z-index: -1;
      display: block;
      background: center / 100% no-repeat url("../../images/green_hover.svg");
    }

    @media screen and (min-width: 1025px) {
      &:hover {
        background-color: transparent;
        border: transparent;
      }
    }
  }
}
