.draggable-wrapper {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;

  &.animate {
    pointer-events: none;
  }
}

.draggable-item {
  width: 300px;
  height: 300px;

  @media screen and (max-width: 1025px) {
    width: 150px;
    height: 150px;
  }

  &.animate {
    transition: top 0.9s ease-in-out, transform 0.9s ease-in-out;
    animation: 2s rotateAndMove forwards ease-in-out 1.5s;
  }

  .img {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    object-fit: contain;
  }

  .img-border {
    // position: absolute;
    // left: -25px;
    // top: -25px;
    // width: 115%;
    // height: 115%;
    // opacity: 0;
    // visibility: hidden;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    object-fit: contain;
    opacity: 0;
    visibility: hidden;
    transition: opacity 0.5s ease, visibility 0.5s ease;

    // @media screen and (max-width: 1025px) {
    //   width: 105%;
    //   height: 105%;
    //   left: -10px;
    //   top: -10px;
    // }
  }

  &.dragging {
    .img {
      opacity: 0;
      visibility: hidden;
    }
    .img-border {
      opacity: 1;
      visibility: visible;
    }
  }
}

@keyframes rotateAndMove {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
